import { References } from "../../References";
import { GameScene, SceneConfigs } from "../../scenes/gameScene";
import { BaseController } from "../BaseController";
import { ControllerManager } from "../ControllerManager";
import { NotificationList } from "../NotificationList";
import { sharedGameControllers } from "../SharedGameControllers";

export class HudController extends BaseController {
  public view: GameScene;
  private sceneConfigs: SceneConfigs;
  private refs: References;
  private currentSceneName: string;
  private currentScene: any;

  private infoSceneOpened: boolean;

  constructor(manager: ControllerManager) {
    super(manager);
    this.infoSceneOpened = false;
    this.refs = sharedGameControllers
      .getController("mainController")
      .getCommonRefs();
  }

  public init(): void {
    super.init();
  }

  public listNotification(): string[] {
    const notifications: string[] = super.listNotification();
    notifications.push(NotificationList.SCENE_DATA_UPDATED);
    notifications.push(NotificationList.ASSETS_LOADED);
    return notifications;
  }

  public handleNotification(message: string, data: any): void {
    switch (message) {
      case NotificationList.SCENE_DATA_UPDATED: {
        this.updateScene(data);
        break;
      }
      case NotificationList.ASSETS_LOADED: {
        this.setConfigs(data);
        break;
      }
    }
  }

  public dispose(): void {
    super.dispose();
  }

  public rewardButtonClicked(): void {
    this.send(NotificationList.REWARD_BUTTON_CLICK);
  }

  public toggleInfoScene(): void {
    this.send(NotificationList.OPEN_INFO_SCENE);
  }

  public collectionButtonClick(id: string): void {
    if (this.currentScene.size === this.currentScene.collected) {
      this.send(NotificationList.COLLECT_BUTTON_CLICK, id);
    }
  }

  public collactibleItemClicked({ index }: any): void {
    const diff = index - this.currentScene.collected;
    if (diff > 1) {
      return;
    } else if (this.currentScene.collected === 0 && diff === 1) {
      this.send(NotificationList.NEXT_BUTTON_CLICK, 0);
    } else {
      this.send(NotificationList.COLLECTIBLE_BUTTON_CLICK, index);
    }
  }

  private updateScene({ data }: any): void {
    this.view.setRefsData(data);
    this.currentScene = data[this.currentSceneName];
    this.view.updateView(this.currentScene.collected);
    this.view.initBottomButtonsHud(this.currentSceneName);
  }

  private setConfigs(data: any): void {
    this.currentSceneName = data.default_scene;
    this.currentScene = this.refs.configs.data[this.currentSceneName];
    this.sceneConfigs = data;
    this.view.setRefsData(this.refs.configs.data);
    this.view.setConfig(data);
    this.view.parsingConfigs();
    this.view.showCharacter(this.currentScene.collected);
    this.view.initBottomButtonsHud(this.currentSceneName);
    this.view.initCollactibleButtons(this.currentScene.collected);
  }
}



