import { MainController } from "../controllers/gameControllers";
import { sharedGameControllers } from "../controllers/SharedGameControllers";
import { Container, Scene, Sprite } from "../engine";
import { SceneConfigs } from "./gameScene";

export class InfoScene extends Scene {
  private sceneConfigs: SceneConfigs;
  private animations: any;
  private particles: any;
  private spines: any;
  private sounds: any;

  private buttons: any;
  private controller: MainController;
  private bg: Container;

  constructor() {
    super();
    this.controller = sharedGameControllers.getController("mainController");
  }

  public showScene({ data }: any): void {
    this.sceneConfigs = data.scenes.info;
    this.animations = {};
    this.particles = {};
    this.spines = {};
    this.sounds = {};
    this.buttons = {};
    this.initElements();
  }

  public initElements(): void {
    const { bg, home } = this.sceneConfigs;
    if (bg && !this.bg) {
      this.bg = this.addChild(new Sprite(bg));
    }
    if (home && !this.buttons.home) {
      const homeButton = new Sprite(home.frame);
      this.addChild(homeButton);
      this.buttons.home = homeButton;
      homeButton.interactive = true;
      homeButton.buttonMode = true;
      homeButton.position.set(home.x, home.y);
      homeButton.on("pointerup", () => this.controller.closeInfoPanel());
    }
  }
}



