import { References } from "../References";
import { BaseController } from "./BaseController";
import { ControllerManager } from "./ControllerManager";
import { NotificationList } from "./NotificationList";
import { sharedGameControllers } from "./SharedGameControllers";

export class LoaderController extends BaseController {
  public configs: any;
  private loader: PIXI.loaders.Loader;
  private refs: References;
  private soundsLoaded: boolean;
  private assetsLoaded: boolean;
  private assetConfigs: any;
  /**
   * Progress percentages.
   */
  private progressPercents: { generic: number; sound: number; total: number };
  /**
   * Generic assets to load.
   */
  private genericAssetsToLoad: number;
  /**
   * Next group of assets to load.
   */
  private assetsLoading: number;

  constructor(manager: ControllerManager) {
    super(manager);
    this.loader = new PIXI.loaders.Loader();
    this.refs = sharedGameControllers
      .getController("mainController")
      .getCommonRefs();
  }

  public listNotification(): string[] {
    const notifications: string[] = super.listNotification();
    return notifications;
  }

  public loadAssets(): void {
    this.soundsLoaded = false;
    this.assetsLoaded = false;
    this.progressPercents = { generic: 0, sound: 0, total: 0 };
    this.assetsLoading = 0;
    this.genericAssetsToLoad = 0;
    const { base_cdn_url, game_type, name } = this.configs;
    const { atlases, animations, spines, sounds } = this.assetConfigs;
    let aIds: string[];

    if (atlases) {
      let atlassId: string;
      let atlassJSON: string;
      const ids: string[] = Object.keys(atlases);
      ids.forEach(id => {
        this.genericAssetsToLoad++;
        this.assetsLoading++;
        atlassId = atlases[id];
        atlassJSON = `${base_cdn_url}/${game_type}/${name}/atlases/${atlassId}.json`;
        this.loader.add(id, atlassJSON);
      });
    }

    if (animations) {
      let animationJSON: string;
      aIds = Object.keys(animations);
      aIds.forEach(aid => {
        this.genericAssetsToLoad++;
        this.assetsLoading++;
        animationJSON = `${base_cdn_url}/${game_type}/${name}/animations/${aid}.json`;
        this.loader.add(aid, animationJSON);
      });
    }

    spines.forEach(({ atlas }: any) => {
      const spineUrl = `${base_cdn_url}/${game_type}/${name}/spine/${atlas}/${atlas}.json`;
      this.loader.add(atlas, spineUrl);
    });

    sounds.forEach((asset: any) => {
      this.genericAssetsToLoad++;
      this.assetsLoading++;
      const audioUrl = `${base_cdn_url}/${game_type}/${name}/sounds/${
        asset.id
      }.mp3`;
      this.refs.assets.sounds[asset.id] = new Howl({
        src: [audioUrl],
        autoplay: false,
        loop: asset.loop || false,
        volume: asset.volume || 1,
        mute: asset.mute || false,
        rate: asset.rate || 1,
        html5: asset.html5 || false,
        onload: () => {
          this.genericAssetsToLoad--;
          this.soundsLoaded = true;
          this.finishAllLoads();
        },
        onloaderror: (soundId: number, error: any) => {
          const loadError =
            error instanceof Error
              ? error
              : new Error(`Error loading sound ${asset.id}`);
        },
      });
    });
    if (sounds.length === 0) {
      this.soundsLoaded = true;
      this.finishAllLoads();
    }

    this.loader.load((_loader: any, refs: any) => {
      this.genericAssetsToLoad--;
      if (aIds.length > 0) {
        aIds.forEach(
          aid => (this.refs.assets.animations[aid] = refs[aid].data.animations),
        );
      }

      spines.forEach(({ atlas }: any) => {
        this.refs.assets.spines[atlas] = refs[atlas].spineData;
      });

      this.assetsLoaded = true;
      this.finishAllLoads();
    });

    this.loader.onProgress.add(this.onGenericAssetProgress.bind(this));
  }

  public loadConfigs(
    baseUrl: string,
    cb: (resourses: any) => void,
    configName: string = "configs",
  ): void {
    this.loader
      .add(configName, baseUrl)
      .load((_loader: any, resourses: any) => cb(resourses));
  }

  public loadSplashScreen(configs: any, { data }: any, cb: () => void): void {
    this.configs = configs;
    this.assetConfigs = data;
    const { base_cdn_url, game_type, name } = configs;
    const imgUrl = `${base_cdn_url}/${game_type}/${name}/images/${
      data.splashscreen
    }`;
    this.loader.add(data.splashscreen, imgUrl);
    this.loader.load(() => cb());
  }

  private finishAllLoads(): void {
    if (this.assetsLoaded && this.soundsLoaded) {
      this.send(NotificationList.ASSETS_LOADED, this.assetConfigs);
    }
  }

  private onGenericAssetProgress(
    loader: PIXI.loaders.Loader,
    resource: PIXI.loaders.Resource,
  ): void {
    // Calculate real percentage (including other loaders)
    const innerIncrement = loader.progress - this.progressPercents.generic;
    this.progressPercents.generic = loader.progress;
    const totalIncrement =
      (innerIncrement * this.genericAssetsToLoad) / this.assetsLoading;
    this.progressPercents.total += totalIncrement;
    this.send(NotificationList.LOADING_PROGRESS, this.progressPercents.total);
  }

  private onAssetsError(args: any): void {
    // window.console.log(`onAssetsError ${args.asset.id}: ${args.error!.message}`);
  }
}



