import { BaseController } from "./BaseController";

class SharedGameControllers {
    private controllers: any;

    constructor() {
        this.controllers = {};
    }

    public setController(name: string, controller: BaseController): void {
        this.controllers[name] = controller;
    }

    public getController(name: string): any {
        return this.controllers[name];
    }
}

export const sharedGameControllers = new SharedGameControllers();



