import { Constants } from "../Constants";
import { References } from "../References";
import { BaseController } from "./BaseController";
import { ControllerManager } from "./ControllerManager";
import { NotificationList } from "./NotificationList";
import { sharedGameControllers } from "./SharedGameControllers";

export class ServerController extends BaseController {
  private refs: References;

  constructor(manager: ControllerManager) {
    super(manager);
  }

  public init(): void {
    super.init();
    this.refs = sharedGameControllers
      .getController("mainController")
      .getCommonRefs();
  }

  public listNotification(): string[] {
    const notifications: string[] = super.listNotification();
    notifications.push(NotificationList.ASSETS_LOADED);
    notifications.push(NotificationList.SEND_INIT_REQUEST);
    notifications.push(NotificationList.SERVER_RECIEVED_INIT);
    notifications.push(NotificationList.REWARD_BUTTON_CLICK);
    notifications.push(NotificationList.COLLECTIBLE_BUTTON_CLICK);
    notifications.push(NotificationList.COLLECT_BUTTON_CLICK);
    notifications.push(NotificationList.NEXT_BUTTON_CLICK); // TEMP till server not send event
    return notifications;
  }

  public handleNotification(message: string, data: any): void {
    switch (message) {
      case NotificationList.SEND_INIT_REQUEST: {
        this.sendInitRequest(this.onDataLoaded);
        break;
      }
      case NotificationList.REWARD_BUTTON_CLICK: {
        this.sendRewardRequest();
        break;
      }
      case NotificationList.COLLECTIBLE_BUTTON_CLICK: {
        this.collectCollectible(data);
        break;
      }
      case NotificationList.NEXT_BUTTON_CLICK: {
        this.openNextEvent(data);
        break;
      }
      case NotificationList.COLLECT_BUTTON_CLICK: {
        this.completeCollection(data);
        break;
      }
    }
  }

  public dispose(): void {
    super.dispose();
  }

  private sendInitRequest(callback: (data: any) => void): void {
    const liveOpsUrl = this.refs.liveOpsUrl;
    const isLocal = this.refs.isLocal;
    // TEMP, refactor later to dev/prod enviroment
    if (isLocal) {
      return callback(Constants.initServerResponece);
    }
    fetch(liveOpsUrl, { method: "POST" })
      .then(response => response.json())
      .then(data => callback(data))
      .catch(error => error);
  }

  private onDataLoaded = (configs: any): void => {
    this.refs.gameData = { ...configs.data, ...{} };
    this.refs.configs = configs;
    this.send(NotificationList.SERVER_RECIEVED_INIT);
  };

  private openNextEvent(index: number): void {
    let data: any = {};
    Constants.testSteps.forEach(step => {
      if (step.step === index + 1) {
        return (data = step);
      }
    });
    this.send(NotificationList.SCENE_DATA_UPDATED, data);
  }

  private collectCollectible(index: number): void {
    const isLocal = this.refs.isLocal;
    if (isLocal) {
      Constants.testSteps.forEach(step => {
        if (step.step === index) {
          return this.send(NotificationList.SCENE_DATA_UPDATED, step);
        }
      });
    }
  }

  private completeCollection(id: string): void {
    const isLocal = this.refs.isLocal;
    if (isLocal) {
      Constants.testSteps.forEach(step => {
        if (step.step === 5) {
          return this.send(NotificationList.SCENE_DATA_UPDATED, step);
        }
      });
    }
  }

  private sendRewardRequest(): void {
    window.console.log("sendRewardRequest to server");
    this.send(
      NotificationList.SCENE_DATA_UPDATED,
      Constants.initServerResponece,
    );
  }
}



