import * as PIXI from "pixi.js";
import {Picture} from "./Picture";

export class Animation extends PIXI.extras.AnimatedSprite  {
    private animation: PIXI.extras.AnimatedSprite;
    private configs: AnimationConfig;

    constructor(frames: any, configs: any) {
        const animatedFrames: any = [];
        frames.forEach((frame: any) => animatedFrames.push(Picture.frame(frame)));
        super(animatedFrames);
        this.animationSpeed = configs.speed;
        this.loop = configs.loop === 0;
        this.play();
    }
}

export interface AnimationConfig {
    id: string;
    x: number;
    y: number;
    loop: number;
    awake: boolean;
    speed: number;
    animations: any;
}



