import { BaseController } from "./BaseController";
export class ControllerManager {
  private observerMap: {};
  private controllers: BaseController[];

  constructor() {
    this.observerMap = {};
    this.controllers = [];
  }

  public register(controller: BaseController): void {
    const notificationsNames: string[] = controller.listNotification();
    for (const name of notificationsNames) {
      this.registerObserver(name, controller);
    }
    this.controllers.push(controller);
  }

  public remove(controller: BaseController): void {
    const notificationsNames: string[] = controller.listNotification();
    for (const name of notificationsNames) {
      this.removeObserver(name, controller);
    }
    const index: number = this.controllers.indexOf(controller);
    this.controllers.splice(index, 1);
  }

  public registerObserver(
    notificationName: string,
    controller: BaseController,
  ): void {
    // @ts-ignore
      if (this.observerMap[notificationName] != null) {
      // @ts-ignore
        this.observerMap[notificationName].push(controller);
    } else {
      // @ts-ignore
        this.observerMap[notificationName] = [controller];
    }
  }

  public removeObserver(
    notificationName: string,
    controller: BaseController,
  ): void {
    // @ts-ignore
      const controllers: BaseController[] = this.observerMap[notificationName];
      for (let i: number = 0; i < controllers.length; i++) {
      if (controllers[i] === controller) {
        controllers.splice(i, 1);
      }
    }
  }

  public send(notificationName: string, data: any): void {
    // @ts-ignore
      const controllers: BaseController[] = this.observerMap[notificationName];
      if (controllers.length) {
      for (const controller of controllers) {
        controller.handleNotification(notificationName, data);
      }
    }
  }

  public onEnterFrame(): void {
    for (const controller of this.controllers) {
      controller.onEnterFrame();
    }
  }
}



