import { Constants } from "../Constants";
import { Container, Scene, Sprite } from "../engine";

export class LoaderScene extends Scene {
  private bgContainer: Container;
  private configs: any;
  private logoSprite: Sprite;
  private bgGray: PIXI.Graphics;
  private whiteBg: PIXI.Graphics;

  constructor() {
    super();
    this.bgContainer = new Container();
    this.addChild(this.bgContainer);
  }

  public showScene({ data }: any): void {
    this.drawInitBg();
    this.drawWhiteBg();
    this.configs = data;

    this.logoSprite = new Sprite(data.splashscreen);
    this.bgContainer.addChild(this.logoSprite);
  }

  public setProgress(progress: number): void {
    this.whiteBg.x = (Constants.size.loader * progress) / 100;
  }

  private drawInitBg(): void {
    this.bgGray = new PIXI.Graphics();
    this.bgGray.beginFill(0x303030, 1);
    this.bgGray.drawRect(0, 0, Constants.size.width, Constants.size.heigth);
    this.bgGray.endFill();
    this.bgContainer.addChild(this.bgGray);
  }

  private drawWhiteBg(): void {
    this.whiteBg = new PIXI.Graphics();
    this.whiteBg.beginFill(0xffffff, 1);
    this.whiteBg.drawRect(0, 0, Constants.size.loader, Constants.size.heigth);
    this.whiteBg.endFill();
    this.bgContainer.addChild(this.whiteBg);
  }
}



