import { Container } from "./index";

export class SpineAnimation {
    public static spine(spineConfig: SpineConfig, data: any, cb: () => void): Container {
        const container = new Container();

        const spineAnimation = new PIXI.spine.Spine(data);
        spineAnimation.scale.set(spineConfig.scale);
        spineAnimation.x = spineConfig.x;
        spineAnimation.y = spineConfig.y;
        spineAnimation.state.setAnimation(0, spineConfig.default, spineConfig.loop);

        spineAnimation.interactive = true;
        spineAnimation.buttonMode = true;

        spineAnimation.on("pointerdown", () => {
            cb();
            spineAnimation.state.setAnimation(0, spineConfig.pointerdown, false);
            spineAnimation.state.addAnimation(0, spineConfig.default, true, 0);
        });
        container.addChild(spineAnimation);
        return container;
    }
}

export interface SpineConfig {
    atlas: string;
    default: string;
    pointerdown: string;
    scale: number;
    x: number;
    y: number;
    loop: boolean;
    actionSound: string;
}



