import "styles/style.css";

import { MainController } from "./controllers/gameControllers";
import { sharedGameControllers } from "./controllers/SharedGameControllers";

window.onload = () => {
    const element = window.document.getElementById("URL");
    // const url = element ? element.innerText : "";

    const mainController = new MainController();
    sharedGameControllers.setController("mainController", mainController);
    mainController.init("DEV");

    if (element) {
        element.remove();
    }
};



