import { Power2, TweenLite, TweenMax } from "gsap";
import { Constants } from "../Constants";
import { Container, Sprite } from "./index";

export class TweenAnimation {
  public static tween(configs: TweenConfig): Container {
    let animation;
    const container = new Container();
    const sprite = new Sprite(configs.frame);
    sprite.anchor.set(0.5, 0.5);
    container.addChild(sprite);
    switch (configs.value) {
      // create scale animation
      case Constants.animationParams.scale:
        sprite.scale.set(configs.from, configs.from);
        container.x = Constants.size.width / 2;
        container.y = Constants.size.heigth / 2;
        animation = TweenMax.to(sprite.scale, configs.duration, {
          x: configs.to,
          y: configs.to,
          ease: Power2.easeOut,
        });
        break;
      // create position animation
      case Constants.animationParams.position:
        container.x = configs.Xfrom || Constants.size.width / 2;
        container.y = configs.Yfrom || Constants.size.heigth / 2;
        if (!configs.Xto) {
          configs.Xto = Constants.size.width;
        }
        if (!configs.Yto) {
          configs.Yto = Constants.size.heigth;
        }
        animation = TweenMax.to(container.position, configs.duration, {
          x: configs.Xto,
          y: configs.Yto,
          ease: Power2.easeOut,
        });
        break;
    }
    return container;
  }
}

export interface TweenConfig {
  frame: string;
  value: string;
  from?: number;
  to?: number;
  Yfrom?: number;
  Yto?: number;
  Xfrom?: number;
  Xto?: number;
  duration: 1;
}



