import {
  Dom,
  PixiAppWrapper as Wrapper,
  pixiAppWrapperEvent as WrapperEvent,
  PixiAppWrapperOptions as WrapperOpts,
} from "pixi-app-wrapper";
import * as PIXI from "pixi.js";
import { Constants } from "../Constants";
import { LoaderController } from "../controllers/LoaderController";
import { References } from "../References";
import { BootScene } from "../scenes/bootScene";
import { GameScene } from "../scenes/gameScene";
import { InfoScene } from "../scenes/infoScene";
import { LoaderScene } from "../scenes/loaderScene";
import { Container, Scene } from "./index";
import { ScenesManager } from "./ScenesManager";

export class Game extends PIXI.Application {
  public app: Wrapper;
  public stage: Container;
  public scene: Scene;
  public mainContainer: Container;
  public loaderController: LoaderController;
  public references: References;
  public scenesManager: ScenesManager;

  private assetConfigs: any;
  private loaderScene: LoaderScene;
  private infoScene: InfoScene;

  constructor() {
    super();
  }

  public setLoader(loader: LoaderController): void {
    this.loaderController = loader;
  }

  public start(): void {
    const canvas = Dom.getElementOrCreateNew<HTMLCanvasElement>(
      "app-canvas",
      "canvas",
      document.getElementById("app-root"),
    );

    const appOptions: WrapperOpts = {
      width: Constants.size.width,
      height: Constants.size.heigth,
      scale: "keep-aspect-ratio",
      align: "middle",
      resolution: window.devicePixelRatio,
      roundPixels: true,
      transparent: false,
      backgroundColor: 0x000000,
      view: canvas,
      showFPS: true,
      changeOrientation: true,
    };

    this.app = new Wrapper(appOptions);
    this.app.on(WrapperEvent.RESIZE_START, () => this.onResizeStart());
    this.app.on(WrapperEvent.RESIZE_END, () => this.onResizeEnd());

    this.scenesManager = new ScenesManager();

    this.mainContainer = new Container();
    this.app.stage.addChild(this.mainContainer);

    this.initScenes();
  }

  public loadConfigs(references: References): void {
    this.references = references;
    this.activateScene("bootScene");
    const { base_cdn_url, game_type, name } = this.references.configs;
    const url = `${base_cdn_url}/${game_type}/${name}/config.json`;
    this.loaderController.loadConfigs(url, ({ configs }) => {
      this.assetConfigs = configs;
      this.loadAssets();
    });
  }

  public loop = () => {
    this.renderer.render(this.app.stage);
    requestAnimationFrame(() => this.loop());
  };

  public loadAssets = () => {
    this.loaderScene = this.scenesManager.createScene(
      "loaderScene",
      LoaderScene,
    );
    this.mainContainer.addChild(this.loaderScene);
    this.loaderController.loadSplashScreen(
      this.references.configs,
      this.assetConfigs,
      () => {
        this.activateScene("loaderScene");
        this.loaderScene.showScene(this.assetConfigs);
        this.loaderController.loadAssets();
      },
    );
  };

  public updateLoader(data: number): void {
    this.loaderScene.setProgress(data);
  }

  public allDownloadsDone = (): void => {
    // const { data } = this.assetConfigs;
    this.activateScene("gameScene");
  };

  public activateScene = (sceneName: string) => {
    // console.log("show scene " + sceneName);
    this.scenesManager.activateScene(sceneName);
  };

  public showInfoScene(): void {
    this.activateScene("infoScene");
    this.infoScene.showScene(this.assetConfigs);
  }

  public closeInfoPanel(): void {
    this.activateScene("gameScene");
  }

  /*
  init game scenes
   */
  private initScenes(): void {
    const boot: BootScene = this.scenesManager.createScene(
      "bootScene",
      BootScene,
    );
    this.mainContainer.addChild(boot);

    const gameScene: GameScene = this.scenesManager.createScene(
      "gameScene",
      GameScene,
    );
    this.mainContainer.addChild(gameScene);

    this.infoScene = this.scenesManager.createScene("infoScene", InfoScene);
    this.mainContainer.addChild(this.infoScene);
  }

  private onResizeStart(): void {
    window.console.log("RESIZE STARTED!");
  }

  private onResizeEnd(): void {
    window.console.log("RESIZE ENDED!");
  }
}



