import { ControllerManager } from "./ControllerManager";

export class BaseController {
    public manager: ControllerManager;
    public view: any;
    public configs: any;

    constructor(manager: ControllerManager) {
        this.manager = manager;
    }

    public init(configs?: any): void {
        this.manager.register(this);
        this.configs = configs;
    }

    public listNotification(): string[] {
        return [];
    }

    public handleNotification(message: string, data: any): void {
    }

    public send(message: string, data: any = null): void {
        this.manager.send(message, data);
    }

    public dispose(): void {
        this.manager.remove(this);
    }

    public onEnterFrame(): void {
    }

    public getGameConfigs(): void {
        return this.configs;
    }

    public setView(view: any): void {
        this.view = view;
    }
}



