import { Constants } from "../Constants";
import { Game } from "../engine/Game";
import { References } from "../References";
import { BaseController } from "./BaseController";
import { ControllerManager } from "./ControllerManager";
import { HudController } from "./hud/HudController";
import { LoaderController } from "./LoaderController";
import { NotificationList } from "./NotificationList";
import { ServerController } from "./ServerController";
import { sharedGameControllers } from "./SharedGameControllers";

export class MainController extends BaseController {
  public manager: ControllerManager;
  private server: ServerController;
  private loaderController: LoaderController;
  private references: References;
  private game: Game;

  constructor() {
    super(new ControllerManager());
    super.init();
  }

  public listNotification(): string[] {
    const notifications: string[] = super.listNotification();
    notifications.push(NotificationList.SERVER_RECIEVED_INIT);
    notifications.push(NotificationList.ASSETS_LOADED);
    notifications.push(NotificationList.LOADING_PROGRESS);
    notifications.push(NotificationList.OPEN_INFO_SCENE);
    return notifications;
  }

  public handleNotification(message: string, data: any): void {
    switch (message) {
      case NotificationList.SERVER_RECIEVED_INIT: {
        this.startDownloadAssets();
        break;
      }
      case NotificationList.ASSETS_LOADED: {
        this.allDownloadsDone();
        break;
      }
      case NotificationList.LOADING_PROGRESS: {
        this.updateLoadProgress(data);
        break;
      }
      case NotificationList.OPEN_INFO_SCENE: {
        this.showInfoScene();
        break;
      }
    }
  }

  public getConfigs(): void {
    this.send(NotificationList.SEND_INIT_REQUEST);
  }

  public startDownloadAssets(): void {
    this.game = new Game();
    this.game.setLoader(this.loaderController);
    this.game.loadConfigs(this.references);
  }

  public init(liveOpsUrl: string): void {
    this.references = new References();
    // TEMP!!!
    this.references.isLocal = liveOpsUrl === "DEV";
    this.references.liveOpsUrl = this.references.isLocal
      ? Constants.LIVE_OPS
      : liveOpsUrl;

    this.server = new ServerController(this.manager);
    this.server.init();
    this.initControllers();
    this.getConfigs();
  }

  public getCommonRefs(): References {
    return this.references;
  }

  public closeInfoPanel(): void {
    this.game.closeInfoPanel();
  }

  private allDownloadsDone(): void {
    this.game.allDownloadsDone();
  }

  private updateLoadProgress(data: any): void {
    this.game.updateLoader(data);
  }

  private showInfoScene(): void {
    this.game.showInfoScene();
  }

  private initControllers(): void {
    this.loaderController = new LoaderController(this.manager);
    // init HUD controller and view
    const hudController = new HudController(this.manager);
    sharedGameControllers.setController("hudController", hudController);
    hudController.init();
  }
}



